<script>
  import { onMount } from "svelte";
  import algoliasearch from "algoliasearch/lite";
  import MediaQuery from "svelte-media-query";
  import MobileSearchLayout from "./MobileSearchLayout.svelte";
  import DesktopSearchLayout from "./DesktopSearchLayout.svelte";
  import Overlay from "../Overlay/index.svelte";

  export let target;
  export let settings = {
    popularSearches: [],
    featuredProducts: [],
  };

  const client = algoliasearch(
    "U6BJKY9F7D",
    "72fb178dcbaa31e20a8b27d5d54db83a"
  );

  const locale = settings.locale;
  const searchIndexName = `sheertex.com_${locale}`;

  const searchSettings = {
    index: client.initIndex(searchIndexName),
    queryPlaceholder: settings.search_query_placeholder,
    clearButton: settings.search_clear_button,
    popularProductsTitle: settings.search_popular_products_title,
    popularQueriesTitle: settings.search_popular_queries_title,
    popularQueries: settings.search_popular_queries_list
      .split(";")
      .map((q) => q.trim()),
    popularProducts: settings.search_popular_products.map((product) => {
      return {
        ...product,
        imageSrc: product.featured_image,
        sticker:
          product.sticker && product.sticker.data
            ? product.sticker.data
            : undefined,
        onlineStoreUrl: `/products/${product.handle}`,
      };
    }),
  };

  let isSearchOpened = false;

  function setBodyOverflow(overflowType) {
    document.body.style.overflow = overflowType;
  }

  onMount(() => {
    target.addEventListener("click", () => {
      if (!isSearchOpened) {
        openSearch();
      }
    });
  });

  function openSearch() {
    isSearchOpened = true;
    setBodyOverflow("hidden");
  }

  function closeSearch() {
    isSearchOpened = false;
    setBodyOverflow("auto");
  }
</script>

{#if settings != null}
  {#if isSearchOpened}
    <MediaQuery query="(max-width: 1024px)" let:matches>
      {#if matches}
        <MobileSearchLayout onClose={closeSearch} settings={searchSettings} />
      {:else}
        <Overlay onClick={closeSearch} />
        <DesktopSearchLayout onClose={closeSearch} settings={searchSettings} />
      {/if}
    </MediaQuery>
  {/if}
{/if}
