<script>
  import { fly } from "svelte/transition";
  import { debounce } from "underscore";

  import SearchHit from "./SearchHit.svelte";

  export let onClose = () => {};

  export let settings = {};

  let query = "";
  let fetching = false;
  const index = settings.index;

  function setQuery(newQuery) {
    if (query === newQuery) {
      return;
    }

    if (!newQuery) {
      query = newQuery;
      searchHits = [];
      return;
    }

    query = newQuery;
    fetching = true;
    searchHits = [];
    index
      .search(newQuery, { hitsPerPage: 100, facetFilters: ["tags:-internal"] })
      .then(({ hits }) => {
        searchHits = hits;
        fetching = false;
      })
      .catch((err) => {
        console.log(
          `Failed to load search results for query ${newQuery}. Error: ${err.toString()}`
        );
      });
  }

  let searchHits = [];
</script>

<div
  class="tw-absolute tw-w-full tw-top-0 tw-left-0 tw-bg-white tw-shadow-md tw-z-20"
  in:fly={{ y: -10, duration: 200 }}
  out:fly={{ y: -10, duration: 200 }}
>
  <div class="tw-w-full tw-max-w-screen-xl tw-mx-auto">
    <div class="tw-px-8 tw-mt-4">
      <div class="tw-flex tw-flex-row tw-content-between tw-items-center">
        <div class="tw-flex tw-flex-row tw-flex-grow tw-items-center">
          <span
            on:click|stopPropagation={onClose}
            class="tw-m-1 tw-cursor-pointer tw-transition-transform tw-duration-500 tw-transform hover:tw-rotate-90 tw-text-xl"
            >&#10005;</span
          >
          <input
            value={query}
            class="tw-ml-4 tw-outline-none tw-border-0 tw-w-full focus:tw-border-0 tw-text-xs tw-font-sans tw-font-medium tw-uppercase tw-tracking-widest tw-leading-tight"
            type="text"
            name="query"
            placeholder={settings.queryPlaceholder}
            on:keyup={debounce(({ target: { value } }) => setQuery(value), 200)}
            autofocus
          />
        </div>
        <span
          on:click|stopPropagation={() => setQuery("")}
          class="tw-m-1 tw-cursor-pointer tw-underline tw-text-xs tw-font-sans tw-font-medium tw-uppercase tw-tracking-widest tw-leading-tight"
          >{settings.clearButton}</span
        >
      </div>
    </div>
  </div>
  <hr />
  <div class="tw-w-full tw-max-w-screen-xl tw-mx-auto">
    <div class="tw-px-8 tw-mt-8 tw-mb-4 tw-max-h-75-screen tw-overflow-y-auto">
      <div class="tw-grid tw-grid-cols-6 tw-gap-4 tw-gap-y-6 tw-cursor-default">
        {#if query === "" || searchHits.length === 0}
          {#if query !== "" && searchHits.length == 0 && !fetching}
            <div class="tw-col-span-6 tw-text-xl tw-font-serif">
              Your search for “{query}” didn’t return any results.
            </div>
          {/if}
          <div class="tw-col-span-2 tw-text-2xl tw-font-serif tw-font-normal">
            {settings.popularQueriesTitle}
          </div>
          <div class="tw-col-span-4 tw-text-2xl tw-font-serif tw-font-normal">
            {settings.popularProductsTitle}
          </div>
          <div
            class="tw-col-span-2 tw-text-xs tw-uppercase tw-font-sans tw-font-medium tw-tracking-widest tw-leading-tight"
          >
            {#each settings.popularQueries as searchSuggestion}
              <div on:click={() => setQuery(searchSuggestion)} class="tw-mb-4">
                <span class="tw-cursor-pointer hover:tw-text-green-400"
                  >{searchSuggestion}</span
                >
              </div>
            {/each}
          </div>
          {#each settings.popularProducts as featuredProduct}
            <SearchHit searchHit={featuredProduct} />
          {/each}
        {:else}
          <div class="tw-col-span-6 tw-text-xl">
            {searchHits.length} result(s)
          </div>
          {#each searchHits as searchHit}
            <SearchHit {searchHit} />
          {/each}
        {/if}
      </div>
    </div>
  </div>
</div>
