<script>
  // Import Swiper Svelte components
  import SwiperCore, { Navigation, Autoplay, Pagination, Mousewheel, Keyboard } from 'swiper';
  import { Swiper, SwiperSlide } from 'swiper/svelte';
  import Slide from './Slide.svelte';
  // Import Swiper styles
  import 'swiper/swiper-bundle.min.css';
  import 'swiper/components/navigation/navigation.min.css';
  import 'swiper/components/pagination/pagination.min.css';

  SwiperCore.use([Navigation, Autoplay, Pagination, Mousewheel, Keyboard]);

  export let slides = [];
</script>

<div class="tw-relative">
  {#if slides.length > 1}
    <Swiper
        slidesperview={1}
        initialslide={0}
        spacebetween={50}
        loop={false}
        autoplay={{
          delay: 7000,
          waitForTransition: true,
          disableOnInteraction: true,
        }}
        pagination={{
          clickable: false,
        }}
        class="myswiper"
        cssMode={true}
        mousewheel={true}
        keyboard={true}
        navigation={{ prevEl: '.swiper-button-prev', nextEl: '.swiper-button-next' }}
        modules={[Autoplay, Navigation, Pagination]}
    >
      {#each slides as slide}
        <SwiperSlide>
          <Slide
            heightD={slide.view_height_desktop}
            heightM={slide.view_height_mobile}
            eyebrow={slide.eyebrow}
            header={slide.header}
            subHeader={slide.subheader}
            shopButtonLink={slide.shop_button_link}
            shopButtonText={slide.shop_button_text}
            pageButtonLink={slide.page_button_link}
            pageButtonText={slide.page_button_text}
            imageSrcD={slide.image_desktop}
            imageSrcM={slide.image_mobile}
            fontColour={slide.font_colour}
            headerSize={slide.header_size}
            buttonCount={slide.button_count}
          />
        </SwiperSlide>
      {/each}
    </Swiper>
  {:else}
    <Swiper
      class="myswiper"
      cssMode={true}
      mousewheel={true}
      keyboard={true}
    >
      {#each slides as slide}
        <SwiperSlide>
          <Slide
            heightD={slide.view_height_desktop}
            heightM={slide.view_height_mobile}
            eyebrow={slide.eyebrow}
            header={slide.header}
            subHeader={slide.subheader}
            shopButtonLink={slide.shop_button_link}
            shopButtonText={slide.shop_button_text}
            pageButtonLink={slide.page_button_link}
            pageButtonText={slide.page_button_text}
            imageSrcD={slide.image_desktop}
            imageSrcM={slide.image_mobile}
            fontColour={slide.font_colour}
            headerSize={slide.header_size}
            buttonCount={slide.button_count}
          />
        </SwiperSlide>
      {/each}
    </Swiper>
  {/if}
</div>

<style>
  :global(div.swiper-button-disabled) {
    opacity: 0.5;
  }
  .swiper-button-prev {
    color: white;
    position: absolute;
    bottom: 0px;
    left: 0px;
  }
  .swiper-button-next {
    color: white;
    position: absolute;
    bottom: 0px;
    right: 0px;
  }
</style>
