<script>
  import { getSizedImageUrl } from "@shopify/theme-images";
  export let searchHit = {};

  const colorOptions = searchHit.options?.find(
    (option) => option.name === "Color" || option.name === "Couleur"
  );
  const colorsCount = colorOptions
    ? colorOptions.values.length
    : searchHit.colors?.length || 0;

  function getImageUrl(searchHit) {
    return searchHit.imageSrc
      ? getSizedImageUrl(searchHit.imageSrc, "300x")
      : "https://via.placeholder.com/300x420";
  }
</script>

<a
  class="tw-cursor-pointer tw-flex tw-flex-col tw-whitespace-normal tw-relative"
  href={searchHit.onlineStoreUrl}
>
  <img
    loading="eager"
    class="tw-w-full tw-mb-2"
    src={getImageUrl(searchHit)}
    alt={searchHit.title}
  />
  <div
    class="hover:tw-text-green-400 tw-w-full tw-mb-2 tw-font-serif tw-font-normal tw-text-base"
  >
    {searchHit.title}
  </div>
  {#if searchHit.sticker && searchHit.sticker.text}
    <div
      class="tw-absolute tw-uppercase tw-top-1 tw-left-1 tw-text-xs tw-py-0.5 tw-rounded-full tw-tracking-wider tw-whitespace-nowrap tw-text-center tw-align-middle"
      style="color: {searchHit.sticker.color}; background-color:{searchHit
        .sticker.backgroundColor}; width:{searchHit.sticker
        .width}; font-family: Agrandir; font-weight: 500; font-size: 0.6rem; letter-spacing: 0.104rem; line-height: 0.6rem; padding: 6px 9px;"
    >
      {searchHit.sticker.text}
    </div>
  {/if}
  {#if colorsCount === 1}
    <div class="tw-text-sm">1 color</div>
  {:else if colorsCount > 0}
    <div class="tw-text-sm">{colorsCount} colors</div>
  {/if}
</a>
