<script>
  import dayjs from "dayjs";
  import utc from "dayjs/plugin/utc";
  import timezone from "dayjs/plugin/timezone";

  export let targetElement;
  export let countdownEndHours = 9;

  dayjs.extend(utc);
  dayjs.extend(timezone);

  function leadZero(num) {
    if (num < 10) {
      return `0${num}`;
    }

    return num;
  }

  function formatDiff(diff) {
    const { hours, minutes, seconds } = diff;
    return `${leadZero(hours)} hours : ${leadZero(minutes)} mins : ${leadZero(
      seconds
    )} secs`;
  }

  function getDiff(targetDate) {
    const seconds = dayjs().diff(
      dayjs.tz(targetDate, "America/Toronto"),
      "seconds"
    );

    let totalSeconds = -1 * seconds;
    const hours = Math.floor(totalSeconds / (60 * 60));
    totalSeconds -= hours * 60 * 60;
    const minutes = Math.floor(totalSeconds / 60);
    totalSeconds -= minutes * 60;

    return {
      hours,
      minutes,
      seconds: totalSeconds,
    };
  }

  function updateElement() {
    // const targetDate = dayjs.tz(dayjs(), "America/Toronto").endOf("d");
    // ^above is original
    // below is where I hardcoded the target time to 9am est

    const currentDateInEST = dayjs.tz(dayjs(), "America/Toronto");
    const startOfDayInEST = currentDateInEST.startOf("d");
    const targetDate = startOfDayInEST.add(countdownEndHours, "hours");
    const isBeforeTargetDate = dayjs().isBefore(targetDate);

    const newText = isBeforeTargetDate
      ? formatDiff(getDiff(targetDate))
      : "00 hours : 00 min : 00 secs";

    targetElement.innerHTML = newText;
  }

  setInterval(() => {
    updateElement();
  }, 1000);
</script>
