import NotificationBanner from "./components/NotificationBanner/index.svelte";
import Search from "./components/Search/index.svelte";
import Slideshow from "./components/Slideshow/index.svelte";
import HPReviewsSlideshow from "./components/HomePage/ReviewsSlider/index.svelte";
import StrengthIconsSlideshow from "./components/HomePage/StrengthIconsSlider/index.svelte";
import Timer from './components/Timer/index.svelte';
import MidnightCountdown from './components/MidnightCountdown/index.svelte';
import FlashSaleCountdown from './components/FlashSaleCountdown/index.svelte';
import GeolocationPopup from './components/GeolocationPopup/index.svelte';

(async function initialize() {
  const slideShowTarget = document.querySelector("[data-svelte=slideshow]");

  if (slideShowTarget) {
    new Slideshow({
      target: slideShowTarget,
      hydrate: false,
      props: {
        slides: JSON.parse(slideShowTarget.getAttribute("data-svelte-payload")),
      },
    });
  }

  const hpReviewslideShowTarget = document.querySelector("[data-svelte=reviews-slideshow]");
  if (hpReviewslideShowTarget) {
    new HPReviewsSlideshow({
      target: hpReviewslideShowTarget,
      hydrate: false,
      props: {
        slides: JSON.parse(hpReviewslideShowTarget.getAttribute("data-svelte-payload")),
      },
    });
  }

  const strengthIconsSlideShowTarget = document.querySelector("[data-svelte=strength-icons-slideshow]");
  if (strengthIconsSlideShowTarget) {
    new StrengthIconsSlideshow({
      target: strengthIconsSlideShowTarget,
      hydrate: false,
      props: {
        slides: JSON.parse(strengthIconsSlideShowTarget.getAttribute("data-svelte-payload")),
      },
    });
  }

  const mainBannerTarget = document.querySelector("[data-svelte=main_banner]");
  if (mainBannerTarget) {
    new NotificationBanner({
      target: mainBannerTarget,
      hydrate: false,
      props: {
        settings: JSON.parse(mainBannerTarget.getAttribute("data-svelte-payload")),
      },
    });
  }

  const secondaryBannerTarget = document.querySelector("[data-svelte=secondary_banner]");
  if (secondaryBannerTarget) {
    new NotificationBanner({
      target: secondaryBannerTarget,
      hydrate: false,
      props: {
        settings: JSON.parse(secondaryBannerTarget.getAttribute("data-svelte-payload")),
      },
    });
  }

  const searchSettingsTarget = document.querySelector("[data-svelte=search-data]");
  if (searchSettingsTarget) {
    const searchPayload = JSON.parse(searchSettingsTarget.getAttribute("data-svelte-payload"));
    const searchTrigger = document.querySelector("[data-svelte=search-trigger]");

    if (searchTrigger) {
      new Search({
        target: searchTrigger,
        hydrate: false,
        props: {
          target: searchTrigger,
          settings: searchPayload,
        },
      });
    }
  }

  const timers = document.querySelectorAll('[data-svelte=timer]');
  if (timers && timers.length > 0) {
    timers.forEach((timerElement) => {
      new Timer({
        target: timerElement,
        hydrate: false,
        props: {
          targetElement: timerElement,
          date: timerElement.getAttribute('date')
        }
      })
    })
  }

  const midnightCoundowns = document.querySelectorAll('[data-svelte=midnight-countdown]');
  if (midnightCoundowns && midnightCoundowns.length > 0) {
    midnightCoundowns.forEach((timerElement) => {
      new MidnightCountdown({
        target: timerElement,
        hydrate: false,
        props: {
          targetElement: timerElement,
          countdownEndHours: timerElement.getAttribute('data-countdown-end')
        }
      })
    })
  }

  const flashSaleCountdown = document.querySelectorAll('[data-svelte=flash-sale-countdown]');
  if (flashSaleCountdown && flashSaleCountdown.length > 0) {
    flashSaleCountdown.forEach((timerElement) => {
      new FlashSaleCountdown({
        target: timerElement,
        hydrate: false,
        props: {
          targetElement: timerElement,
          schedule: timerElement.getAttribute("data-schedule")
        }
      })
    })
  }

  const geolocationPopupTarget = document.querySelector("[data-svelte=geolocation-popup]");
  if (geolocationPopupTarget) {
    new GeolocationPopup({
      target: geolocationPopupTarget,
      hydrate: false,
      props: {
        settings: { domain: geolocationPopupTarget.getAttribute("data-domain") },
      }
    })
  }

})();

let app;
export default app;
