<script>
  export let heightD;
  export let heightM;
  export let eyebrow;
  export let header;
  export let subHeader;
  export let shopButtonLink;
  export let shopButtonText;
  export let pageButtonLink;
  export let pageButtonText;
  export let imageSrcD;
  export let imageSrcM;
  export let fontColour;
  export let headerSize;
  export let buttonCount;
  import { afterUpdate } from 'svelte';

  import dayjs from 'dayjs';
  import utc from 'dayjs/plugin/utc';
  import timezone from 'dayjs/plugin/timezone';

  dayjs.extend(utc);
  dayjs.extend(timezone);

  import { getSizedImageUrl } from '@shopify/theme-images';

  function leadZero(num) {
    if (num < 10) {
      return `0${num}`;
    }
    
    return num;
  }

  function newLineToBr(text) {
    return text.replace(/\n/g, '<br/>');
  }

  function formatDiff(diff) {
    const { hours, minutes, seconds } = diff;
    return `${leadZero(hours)}h:${leadZero(minutes)}m:${leadZero(seconds)}s`;
  }

  function getDiff(targetDate) {
    const seconds = dayjs().diff(dayjs.tz(targetDate, 'America/Los_Angeles'), 'seconds');

    let totalSeconds = -1 * seconds;
    const hours = Math.floor(totalSeconds / (60 * 60));
    totalSeconds -= hours * 60 * 60;
    const minutes = Math.floor(totalSeconds / 60);
    totalSeconds -= minutes * 60;

    return {
      hours,
      minutes,
      seconds: totalSeconds,
    };
  }

  function showTimerIfNeeded(text) {
    if (!text) {
      return;
    }

    const timeRegex = /#time\((?<date>.+)\)/m;
    const match = text.match(timeRegex);

    if (match) {
      const targetDate = match.groups.date;

      return text.replace(match[0], formatDiff(getDiff(targetDate)));
    }

    return text;
  }

  let updateIntervalRef = null;

  function getSubHeaderText() {
    if (updateIntervalRef) {
      clearInterval(updateIntervalRef);
    }

    updateIntervalRef = setInterval(() => {
      subheader = getSubHeaderText();
    }, 1000);

    return showTimerIfNeeded(newLineToBr(subHeader));
  }

  let subheader = getSubHeaderText();

  afterUpdate(() => {
    subheader = getSubHeaderText();
  });
</script>

<!-- desktop hero -->
