    </p>
    {#if buttonCount === "one"} 
      <a
      href={shopButtonLink}
      class="tw-bg-green-200 tw-rounded-3xl tw-uppercase tw-font-medium tw-font-sans tw-py-3 tw-px-20 tw-text-xs tw-cursor-pointer tw-tracking-heading"
      >
        {@html newLineToBr(shopButtonText)}
      </a>
    {:else}
      <span class="tw-grid tw-grid-cols-2">
        <div>
          <a href="{shopButtonLink}" 
              class="tw-bg-green-200 tw-rounded-3xl tw-uppercase tw-font-medium tw-font-sans tw-py-3 tw-px-20 tw-text-xs tw-cursor-pointer tw-tracking-heading">
              <span class="active:tw-text-white">{@html newLineToBr(shopButtonText)}</span>
          </a>
        </div>
        <div>
          <a href="{pageButtonLink}" 
              class="tw-bg-white tw-rounded-3xl tw-uppercase tw-font-medium tw-font-sans tw-py-3 tw-px-20 tw-text-xs tw-cursor-pointer tw-tracking-heading">
              <span class="active:tw-text-white">{@html newLineToBr(pageButtonText)}</span>
          </a>
        </div>
      </span>
    {/if}
  </div>
</div>

