<style>
    :global(div.swiper-button-disabled) {
        opacity: 0.5;
    }
</style>

<div class="tw-relative">
    <Swiper        
        slidesPerView="{1}"
        initialSlide="{1}"
        loop="{true}"
        pagination='{{
            "clickable": true
        }}'
        class="myswiper"
        cssMode="{false}" mousewheel="{true}" keyboard="{true}"
        navigation={{nextEl: '[data-swiper=next]', prevEl: '[data-swiper=prev]'}}
        modules={[Navigation, Pagination]}>
        {#each slides as slide}
            <SwiperSlide>
                <Slide 
                    strengthIcon={slide.strength_icon_svg} 
                    strengthTitle={slide.strength_title} 
                    strengthDesc={slide.strength_desc}                  
                    strengthLink={slide.strength_link}                  
                />
            </SwiperSlide>
        {/each}        
    </Swiper>
</div>

<script>
    // Import Swiper Svelte components
    import SwiperCore, {Navigation, Pagination, Mousewheel, Keyboard} from "swiper";
    import {Swiper, SwiperSlide} from "swiper/svelte";
    import Slide from "./Slide.svelte";
    // Import Swiper styles
    import "swiper/swiper-bundle.min.css";
    import "swiper/components/navigation/navigation.min.css";
    import "swiper/components/pagination/pagination.min.css";

    SwiperCore.use([Navigation, Pagination, Mousewheel, Keyboard]);
    
    export let slides = [];
</script>