<script>
  import dayjs from "dayjs";
  import utc from "dayjs/plugin/utc";
  import timezone from "dayjs/plugin/timezone";

  export let targetElement;
  export let date;

  dayjs.extend(utc);
  dayjs.extend(timezone);

  function leadZero(num) {
    if (num < 10) {
      return `0${num}`;
    }

    return num;
  }

  function formatDiff(diff) {
    const { hours, minutes, seconds } = diff;
    return `${leadZero(hours)}h:${leadZero(minutes)}m:${leadZero(seconds)}s`;
  }

  function getDiff(targetDate) {
    const seconds = dayjs().diff(
      dayjs.tz(targetDate, "America/Toronto"),
      "seconds"
    );

    let totalSeconds = -1 * seconds;
    const hours = Math.floor(totalSeconds / (60 * 60));
    totalSeconds -= hours * 60 * 60;
    const minutes = Math.floor(totalSeconds / 60);
    totalSeconds -= minutes * 60;

    return {
      hours,
      minutes,
      seconds: totalSeconds,
    };
  }

  function updateElement() {
    const targetDate = date;
    const isBeforeTargetDate = dayjs().isBefore(
      dayjs.tz(targetDate, "America/Toronto")
    );

    const newText = isBeforeTargetDate
      ? formatDiff(getDiff(targetDate))
      : "00:00:00";

    targetElement.innerHTML = newText;
  }

  setInterval(() => {
    updateElement();
  }, 1000);
</script>
