<script>
  import dayjs from "dayjs";
  import utc from "dayjs/plugin/utc";
  import timezone from "dayjs/plugin/timezone";

  export let targetElement;
  export let schedule;

  dayjs.extend(utc);
  dayjs.extend(timezone);

  function leadZero(num) {
    if (num < 10) {
      return `0${num}`;
    }

    return num;
  }

  function formatDiff(diff) {
    const { hours, minutes, seconds } = diff;
    return `${leadZero(hours)} hours : ${leadZero(minutes)} mins : ${leadZero(
      seconds
    )} secs`;
  }

  function getDiff(targetDate) {
    const seconds = dayjs().diff(
      dayjs.tz(targetDate, "America/Toronto"),
      "seconds"
    );

    let totalSeconds = -1 * seconds;
    const hours = Math.floor(totalSeconds / (60 * 60));
    totalSeconds -= hours * 60 * 60;
    const minutes = Math.floor(totalSeconds / 60);
    totalSeconds -= minutes * 60;

    return {
      hours,
      minutes,
      seconds: totalSeconds,
    };
  }
  function parseSchedule(schedule) {
    if (!schedule) {
      return [];
    }

    const parsedSchedule = schedule.split(",");
    if (parsedSchedule.length === 0) {
      return [];
    }

    let result = parsedSchedule.map((item) => {
      return parseInt(item.trim(), 10);
    });

    result.sort((a, b) => a - b);

    return result;
  }

  function getTargetDateBySchedule() {
    const currentDateInEST = dayjs.tz(dayjs(), "America/Toronto");
    const startOfDayInEST = currentDateInEST.startOf("d");

    const scheduleIntervals = parseSchedule(schedule);
    if (scheduleIntervals.length === 0) {
      return;
    }

    for (let interval of scheduleIntervals) {
      const currentInterval = startOfDayInEST.add(interval, "hours");
      if (currentDateInEST.isBefore(currentInterval)) {
        return currentInterval;
      }
    }

    const nextDayInterval = startOfDayInEST
      .add(1, "day")
      .add(scheduleIntervals[0], "hours");

    return nextDayInterval;
  }

  function updateElement() {
    const targetDate = getTargetDateBySchedule();
    const newText = formatDiff(getDiff(targetDate));
    targetElement.innerHTML = newText;
  }

  setInterval(() => {
    updateElement();
  }, 1000);
</script>
