<script>
  import { onMount } from "svelte";
  import store from "storejs";
  import Overlay from "./Overlay.svelte";
  import { avaialableStores } from "./availableStores";

  const HIDDEN_STATE = "HIDDEN_STATE";
  const REDIRECT_STATE = "REDIRECT_STATE";
  const NOSHIPPING_STATE = "NOSHIPPING_STATE";

  let popupState = HIDDEN_STATE;
  let redirectToStore = null;
  let currentStore = null;
  let geoLocation = null;

  export let settings = {
    domain: null,
  };

  function setBodyOverflow(overflowType) {
    document.body.style.overflow = overflowType;
  }

  function getPopupStoreKey() {
    return `sheertex-geolocation-popup`;
  }

  function wasPopupShownBefore() {
    return store(getPopupStoreKey()) || false;
  }

  function closePopup() {
    setBodyOverflow("auto");
    store.set(getPopupStoreKey(), true);
    popupState = HIDDEN_STATE;
  }

  function visitStoreAndClosePopup(storeUrl) {
    store.set(getPopupStoreKey(), true);
    window.open(storeUrl, "_self");
  }

  /* getting geolocation from global cache */
  function getGeolocationCountryCode() {
    if (window.__shopper__ && window.__shopper__.geo) {
      return window.__shopper__.geo;
    }
    return null;
  }

  async function waitForCountryCode() {
    while (!getGeolocationCountryCode()) {
      await new Promise((resolve) => setTimeout(resolve, 500));
    }

    return getGeolocationCountryCode();
  }

  onMount(() => {
    if (!settings.domain) {
      return;
    }

    if (wasPopupShownBefore()) {
      return;
    }

    const currentDomain = settings.domain.toLowerCase().replace("www.", "");

    currentStore = avaialableStores.find(
      (store) => store.domain === currentDomain
    );
    if (!currentStore) {
      console.error(
        `GeoLocation popup failed to find current store [${currentDomain}]. Check availableStores.js`
      );
      return;
    }

    waitForCountryCode().then((geo) => {
      geoLocation = geo;

      if (geoLocation.country_name === "Not found") {
        geoLocation.country_name = "your country";
      }

      const countryCode = geo.country_code.trim().toUpperCase();
      redirectToStore = avaialableStores.find((store) =>
        store.countries.includes(countryCode)
      );

      if (!redirectToStore) {
        popupState = NOSHIPPING_STATE;
        setBodyOverflow("hidden");
        return;
      }

      const isTheSameDomain = currentStore.domain === redirectToStore.domain;

      if (!isTheSameDomain) {
        popupState = REDIRECT_STATE;
        setBodyOverflow("hidden");
      } else {
        popupState = HIDDEN_STATE;
        closePopup();
      }
    });
  });
</script>

{#if popupState !== HIDDEN_STATE}
  <Overlay />
  {#if popupState === REDIRECT_STATE}
    <div class="tw-absolute tw-w-full tw-z-30" style="top: 10vh;">
      <div
        class="tw-mx-auto tw-max-w-sm tw-flex tw-flex-col tw-items-center tw-whitespace-normal tw-font-serif tw-font-normal tw-text-base tw-bg-white"
        style="max-width: 28rem; text-align: center;"
      >
        <div
          class="tw-flex tw-flex-col tw-items-center tw-w-full"
          style="padding: 2rem 4rem; padding-bottom: 0; text-align: center;"
        >
          <span class="tw-uppercase tw-text-xs tw-font-sans tw-font-medium"
            >Oh, hey there!</span
          >
          <h2 class="tw-my-5 tw-text-4xl">
            Where In The<br />World Are You?
          </h2>
          <p>
            You’ve landed on our {currentStore.shortName} website—click the button
            below to be redirected to our local site.
          </p>
          <div class="tw-flex tw-flex-col tw-w-full">
            <button
              class="tw-cursor-pointer tw-uppercase tw-w-full tw-my-6"
              style="background-color: #ddff98; font-weight: 500; color: black; padding: 1rem 0; letter-spacing: 0.104rem;"
              on:click={() => visitStoreAndClosePopup(redirectToStore.storeUrl)}
              >Take me to the {redirectToStore.shortName} store</button
            >
            <button
              class="tw-cursor-pointer tw-underline"
              on:click={() => {
                closePopup();
              }}>Stay here</button
            >
          </div>
          <p class="tw-text-xs tw-my-6 tw-text-gray-400">
            Or please select your country below so we can display the correct
            prices, delivery times and delivery costs for your location.
          </p>
        </div>
        <div
          class="tw-w-full tw-flex tw-flex-row tw-items-start tw-justify-center"
          style="padding: 0 0.5rem 4rem 0.5rem; "
        >
          {#each avaialableStores as store}
            <div
              class="tw-flex tw-flex-col tw-inline-block tw-items-center tw-justify-center tw-uppercase tw-cursor-pointer"
              on:click={() => visitStoreAndClosePopup(store.storeUrl)}
            >
              <img
                class="tw-p-2"
                height="45"
                src={store.storeFlagUri}
                alt={store.storeCountryName}
              />
              <span class="tw-font-sans tw-text-xs tw-text-gray-400"
                >{store.storeCountryName}</span
              >
            </div>
          {/each}
        </div>
      </div>
    </div>
  {/if}
  {#if popupState === NOSHIPPING_STATE}
    <div class="tw-absolute tw-w-full tw-z-30" style="top: 25vh;">
      <div
        class="tw-mx-auto tw-max-w-sm tw-flex tw-flex-col tw-items-center tw-whitespace-normal tw-font-serif tw-font-normal tw-text-base tw-bg-white"
        style="max-width: 28rem; text-align: center;"
      >
        <div
          class="tw-flex tw-flex-col tw-items-center tw-w-full"
          style="padding: 2rem 4rem; padding-bottom: 0; text-align: center;"
        >
          <span class="tw-uppercase tw-text-xs tw-font-sans tw-font-medium" />
          <h2 class="tw-my-5 tw-text-4xl">Oh, no!</h2>
          <p>
            It seems like we don't ship to {geoLocation.country_name} yet...
          </p>
          <p>
            If you would like to get more information on shipping to {geoLocation.country_name}
            please contact hello@sheertex.com.
          </p>
          <button
            class="tw-cursor-pointer tw-uppercase tw-w-full tw-my-6"
            style="background-color: #ddff98; font-weight: 500; color: black; padding: 1rem 0; letter-spacing: 0.104rem;"
            on:click={() => closePopup()}
            >I still want to explore products</button
          >
        </div>
      </div>
    </div>
  {/if}
{/if}
